@import url(https://fonts.googleapis.com/css?family=Raleway:400,500,600,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600,700);
.body-bg {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow: hidden;
    color: #fff;
}


.flexContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
    margin-bottom: 80px;
}

.inputStyle{
    font-size: 1.3em;
    padding: 10px 10px 10px 40px;
    display: inline-block;
    width: 30%;
    box-shadow: 0 2px 30px 0 rgba(0, 0, 0, 0.08);
    color: #6084a4;
    border: #fff;
    border-radius: 10px;
}

.inputStyle:focus {
    outline: none;
}

@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 480px)
  and (-webkit-min-device-pixel-ratio: 2) {
      .flexContainer {
          margin-bottom: 50px;
      }
  }
section.slideshow {
    position: relative;
}
section.slideshow div.slide-holder{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
}
section.slideshow section.current-slide{
    width: 650px;
    height: 450px;
    /* margin: auto 10px; */
    margin: 0 auto;
}
/* section.slideshow section.previous-slide,
section.slideshow section.next-slide{
    width: 200px;
    height: 150px;
} */
section.slideshow section.slide div.slide-thumbnail{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    border-radius: 6px;
}

section.slideshow div.slideshow-controller{
    /* margin-top: 10px; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* margin: 80px 140px 0 140px; */
}

.left-control {
    position: absolute;
    top: 45%;
    left: 15%;
}

.right-control {
    position: absolute;
    top: 45%;
    right: 15%;
}

section.slideshow div.slideshow-controller span{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: none;
    background: #5e5c5c;
    color: #f5f5f5;
    cursor: pointer;
    /* border-radius: 5px; */
    text-align: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
section.slideshow div.slideshow-controller span:hover{
    color: #ffffff;
    background-color: #000000;
}
section.slideshow div.slideshow-controller span:active{
    color: #ffffff;
    background-color: #000000;
}

.unsplash-image {
    width: 100%;
    height: 100%;
    /* border: 3px solid white; */
    border-radius: 10px;
}

@media only screen 
  and (min-device-width: 768px) 
  and (max-device-width: 1024px) 
  and (-webkit-min-device-pixel-ratio: 1) {
    section.slideshow section.current-slide {
        width: 520px;
        height: 430px;
    }
    .left-control {
        left: 5%;
    }
    .right-control {
        right: 5%;
    }
}

@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 480px)
  and (-webkit-min-device-pixel-ratio: 2) {
    section.slideshow section.current-slide {
        width: 260px;
        height: 270px;
    }
    .left-control {
        left: 0;
    }
    .right-control {
        right: 0;
    }
    section.slideshow div.slideshow-controller span {
        width: 20px;
        height: 20px;
    }
  }
.heading {
    font-size: 30px;
    text-align: center;
    font-weight: 500;
    font-family: 'Raleway';
}

.content {
    margin-top: 40px;
    padding: 80px;
}

.icon-div {
    /* display: inline-block; */
    /* width: 33.33%; */
    text-align: center;
}

.img-div {
    height: 115px;
    width: 115px;
    border-radius: 50%;
    border: 3px solid #ffffff;
    margin: 0 auto;
    position: relative;
}

.number-circle {
    height: 30px;
    width: 30px;
    border-radius: 50%;
    color: #ffffff;
    position: absolute;
    left: -5px;
    top: 14px;
}

.number {
    text-align: center;
    line-height: 30px;
}

.number-circle1 {
    background-color: #fa485b;
}

.number-circle2 {
    background-color: #ff7a00;
}

.number-circle3 {
    background-color: #1fcfcc;
}

.icon {
    margin: 30px auto;
    display: block;
}

.img-div2:before {
    content: url("/assets/images/arrow1.png");
    position: absolute;
    left: -274px;
    top: -40px;
    /* width: 100px; */
}

.img-div2:after {
    content: url("/assets/images/arrow2.png");
    right: -236px;
    top: 70px;
    position: absolute;
}

.heading-beneath-icon {
    text-align: center;
    padding-top: 20px;
    color: #ffffff;
    font-size: 18px;
    padding-bottom: 20px;
    font-family: 'Open Sans';
    font-weight: 400;
}

@media screen 
  and (min-device-width: 1200px) 
  and (max-device-width: 1600px) 
  and (-webkit-min-device-pixel-ratio: 1) { 
    .icon-div {
        display: inline-block;
        width: 33.33%;
    }
}

/* tablets */
@media only screen 
  and (min-device-width: 768px) 
  and (max-device-width: 1024px) 
  and (-webkit-min-device-pixel-ratio: 1) {
      .content {
          padding: 10px;
      }
      .icon {
          width: 35px;
          margin: 20px auto;
      }
      .img-div {
          height: 70px;
          width: 70px;
      }
      .heading-beneath-icon {
          padding-top: 0;
      }
      .img-div2:before {
          display: none;
      }
      .img-div2::after {
          display: none;
      }
}

/* mobile devices */
@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 480px)
  and (-webkit-min-device-pixel-ratio: 2) {
    .heading {
        font-size: 20px;
        -webkit-margin-before: 0;
                margin-block-start: 0;
    }
    .content {
        padding: 10px;
    }
    .icon {
        width: 35px;
        margin: 20px auto;
    }
    .img-div {
        height: 70px;
        width: 70px;
    }
    .heading-beneath-icon {
        padding-top: 0;
    }
    .img-div2:before {
        display: none;
    }
    .img-div2::after {
        display: none;
    }
    .number-circle {
        height: 25px;
        width: 25px;
        left: -10px;
    }
    .number {
        line-height: 25px;
    }
} 
